import React from "react";

const About = () => {
  return (
    <div>
      <h1>About</h1>
      <p>
        The SWAPI Graph provides a way for non-technical Star Wars fans to
        consume data from the <a href="https://swapi.co/">SWAPI API</a>. The
        graph helps show how all the data within the API is related through a
        nodes and edges.
      </p>
      <h2>Photo Credits</h2>
      All photos from Rober Gonz√°lez at{" "}
      <a href="https://unsplash.com/photos/ShXbeohihGo">Unsplash</a>
    </div>
  );
};

export default About;



